"use client";

import { useState } from "react";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Wallet, CreditCard, CheckCircle2 } from "lucide-react";
import { toast } from "sonner";
import api from "@/lib/axios";
import { initializePaystack } from "@/lib/paystack";
import { cn } from "@/lib/utils";

interface TopUpModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

const PRESET_AMOUNTS = [10, 20, 50, 100, 200, 500];

export function TopUpModal({ isOpen, onClose, onSuccess }: TopUpModalProps) {
    const [amount, setAmount] = useState<number>(50);
    const [customAmount, setCustomAmount] = useState<string>("");
    const [loading, setLoading] = useState(false);
    const [selectedPreset, setSelectedPreset] = useState<number>(50);

    const handlePresetClick = (value: number) => {
        setSelectedPreset(value);
        setAmount(value);
        setCustomAmount("");
    };

    const handleCustomAmountChange = (value: string) => {
        setCustomAmount(value);
        const numValue = parseFloat(value);
        if (!isNaN(numValue) && numValue > 0) {
            setAmount(numValue);
            setSelectedPreset(0);
        }
    };

    const handleTopUp = async () => {
        if (amount < 1) {
            toast.error("Amount must be at least GHS 1");
            return;
        }

        setLoading(true);

        try {
            // Get user data
            const userData = localStorage.getItem('user');
            if (!userData) {
                toast.error("Please login to continue");
                return;
            }

            const user = JSON.parse(userData);

            // Initialize payment with Paystack
            const response = await api.post('/paystack/initialize', {
                amount: amount
            });

            if (response.data.status) {
                const { authorization_url, reference } = response.data.data;

                // Initialize Paystack popup
                await initializePaystack({
                    key: process.env.NEXT_PUBLIC_PAYSTACK_PUBLIC_KEY || 'pk_test_xxxxx',
                    email: user.email,
                    amount: amount,
                    ref: reference,
                    onSuccess: async (paystackResponse: any) => {
                        // Verify payment
                        try {
                            const verifyResponse = await api.post('/paystack/verify', {
                                reference: paystackResponse.reference
                            });

                            if (verifyResponse.data.status) {
                                toast.success(`Wallet topped up with GHS ${amount}!`);
                                onSuccess();
                                onClose();
                            } else {
                                toast.error("Payment verification failed");
                            }
                        } catch (error) {
                            toast.error("Failed to verify payment");
                        } finally {
                            setLoading(false);
                        }
                    },
                    onClose: () => {
                        setLoading(false);
                        toast.info("Payment cancelled");
                    }
                });
            } else {
                toast.error("Failed to initialize payment");
                setLoading(false);
            }
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Failed to initialize payment");
            setLoading(false);
        }
    };

    return (
        <Dialog open={isOpen} onOpenChange={onClose}>
            <DialogContent className="sm:max-w-md">
                <DialogHeader>
                    <DialogTitle className="flex items-center gap-2 text-2xl">
                        <div className="w-10 h-10 bg-primary/10 rounded-xl flex items-center justify-center">
                            <Wallet className="w-5 h-5 text-primary" />
                        </div>
                        Top Up Wallet
                    </DialogTitle>
                    <DialogDescription>
                        Add funds to your wallet using Paystack. Secure and instant.
                    </DialogDescription>
                </DialogHeader>

                <div className="space-y-6 py-4">
                    {/* Preset Amounts */}
                    <div className="space-y-3">
                        <Label className="text-sm font-bold uppercase tracking-wider text-muted-foreground">
                            Quick Select
                        </Label>
                        <div className="grid grid-cols-3 gap-2">
                            {PRESET_AMOUNTS.map((preset) => (
                                <button
                                    key={preset}
                                    type="button"
                                    onClick={() => handlePresetClick(preset)}
                                    className={cn(
                                        "py-3 px-4 rounded-xl border-2 transition-all font-bold text-sm",
                                        selectedPreset === preset
                                            ? "border-primary bg-primary/10 text-primary"
                                            : "border-gray-200 bg-gray-50 text-gray-700 hover:border-gray-300"
                                    )}
                                >
                                    GHS {preset}
                                </button>
                            ))}
                        </div>
                    </div>

                    {/* Custom Amount */}
                    <div className="space-y-2">
                        <Label className="text-sm font-bold uppercase tracking-wider text-muted-foreground">
                            Or Enter Custom Amount
                        </Label>
                        <div className="relative">
                            <span className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground font-bold">
                                GHS
                            </span>
                            <Input
                                type="number"
                                placeholder="0.00"
                                value={customAmount}
                                onChange={(e) => handleCustomAmountChange(e.target.value)}
                                className="pl-14 h-12 rounded-xl bg-gray-50 border-gray-200 focus:border-primary focus:ring-primary/20 text-lg font-bold"
                                min="1"
                                step="0.01"
                            />
                        </div>
                    </div>

                    {/* Summary */}
                    <div className="bg-primary/5 rounded-2xl p-4 border border-primary/10">
                        <div className="flex items-center justify-between mb-2">
                            <span className="text-sm text-muted-foreground">Amount to add:</span>
                            <span className="text-2xl font-black text-primary">GHS {amount.toFixed(2)}</span>
                        </div>
                        <div className="flex items-center gap-2 text-xs text-muted-foreground">
                            <CheckCircle2 className="w-3 h-3 text-green-600" />
                            <span>Instant credit â€¢ Secure payment via Paystack</span>
                        </div>
                    </div>

                    {/* Action Buttons */}
                    <div className="flex gap-3">
                        <Button
                            variant="outline"
                            onClick={onClose}
                            className="flex-1 rounded-xl"
                            disabled={loading}
                        >
                            Cancel
                        </Button>
                        <Button
                            onClick={handleTopUp}
                            disabled={loading || amount < 1}
                            className="flex-1 rounded-xl shadow-lg shadow-primary/20"
                        >
                            <CreditCard className="w-4 h-4 mr-2" />
                            {loading ? "Processing..." : "Pay Now"}
                        </Button>
                    </div>
                </div>
            </DialogContent>
        </Dialog>
    );
}
