import { useEffect } from 'react';

declare global {
    interface Window {
        PaystackPop: any;
    }
}

export interface PaystackConfig {
    key: string;
    email: string;
    amount: number; // in kobo/pesewas
    currency?: string;
    ref?: string;
    onSuccess: (response: any) => void;
    onClose: () => void;
}

/**
 * Load Paystack inline script
 */
export function loadPaystackScript(): Promise<void> {
    return new Promise((resolve, reject) => {
        if (window.PaystackPop) {
            resolve();
            return;
        }

        const script = document.createElement('script');
        script.src = 'https://js.paystack.co/v1/inline.js';
        script.async = true;
        script.onload = () => resolve();
        script.onerror = () => reject(new Error('Failed to load Paystack script'));
        document.body.appendChild(script);
    });
}

/**
 * Initialize Paystack payment
 */
export async function initializePaystack(config: PaystackConfig): Promise<void> {
    await loadPaystackScript();

    const handler = window.PaystackPop.setup({
        key: config.key,
        email: config.email,
        amount: Math.round(config.amount * 100), // Convert to pesewas
        currency: config.currency || 'GHS',
        ref: config.ref || `${Date.now()}`,
        callback: config.onSuccess,
        onClose: config.onClose,
    });

    handler.openIframe();
}

/**
 * Hook to load Paystack script on component mount
 */
export function usePaystack() {
    useEffect(() => {
        loadPaystackScript().catch(console.error);
    }, []);

    return { initializePaystack };
}
