"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Calendar as CalendarIcon, Filter, X, Search } from "lucide-react";
import { format } from "date-fns";
import { cn } from "@/lib/utils";

export interface TransactionFilters {
    dateRange: 'today' | 'week' | 'month' | 'custom' | 'all';
    type: 'all' | 'credit' | 'debit';
    status: 'all' | 'success' | 'pending' | 'failed';
    search: string;
    customStartDate?: Date;
    customEndDate?: Date;
}

interface TransactionFilterProps {
    filters: TransactionFilters;
    onFiltersChange: (filters: TransactionFilters) => void;
    onExport?: () => void;
}

export function TransactionFilter({ filters, onFiltersChange, onExport }: TransactionFilterProps) {
    const [showCustomDate, setShowCustomDate] = useState(false);

    const handleDateRangeChange = (value: string) => {
        const newFilters = { ...filters, dateRange: value as TransactionFilters['dateRange'] };
        if (value !== 'custom') {
            newFilters.customStartDate = undefined;
            newFilters.customEndDate = undefined;
            setShowCustomDate(false);
        } else {
            setShowCustomDate(true);
        }
        onFiltersChange(newFilters);
    };

    const handleReset = () => {
        onFiltersChange({
            dateRange: 'all',
            type: 'all',
            status: 'all',
            search: '',
        });
        setShowCustomDate(false);
    };

    const hasActiveFilters = filters.dateRange !== 'all' || filters.type !== 'all' ||
        filters.status !== 'all' || filters.search !== '';

    return (
        <div className="space-y-4">
            <div className="flex flex-wrap items-center gap-3">
                {/* Search */}
                <div className="relative flex-1 min-w-[200px]">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                    <Input
                        placeholder="Search by reference..."
                        value={filters.search}
                        onChange={(e) => onFiltersChange({ ...filters, search: e.target.value })}
                        className="pl-10 rounded-xl bg-gray-50 border-gray-200"
                    />
                </div>

                {/* Date Range */}
                <Select value={filters.dateRange} onValueChange={handleDateRangeChange}>
                    <SelectTrigger className="w-[140px] rounded-xl bg-gray-50 border-gray-200">
                        <SelectValue placeholder="Date Range" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">All Time</SelectItem>
                        <SelectItem value="today">Today</SelectItem>
                        <SelectItem value="week">This Week</SelectItem>
                        <SelectItem value="month">This Month</SelectItem>
                        <SelectItem value="custom">Custom Range</SelectItem>
                    </SelectContent>
                </Select>

                {/* Type Filter */}
                <Select value={filters.type} onValueChange={(value) => onFiltersChange({ ...filters, type: value as TransactionFilters['type'] })}>
                    <SelectTrigger className="w-[130px] rounded-xl bg-gray-50 border-gray-200">
                        <SelectValue placeholder="Type" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">All Types</SelectItem>
                        <SelectItem value="credit">Credit</SelectItem>
                        <SelectItem value="debit">Debit</SelectItem>
                    </SelectContent>
                </Select>

                {/* Status Filter */}
                <Select value={filters.status} onValueChange={(value) => onFiltersChange({ ...filters, status: value as TransactionFilters['status'] })}>
                    <SelectTrigger className="w-[130px] rounded-xl bg-gray-50 border-gray-200">
                        <SelectValue placeholder="Status" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">All Status</SelectItem>
                        <SelectItem value="success">Success</SelectItem>
                        <SelectItem value="pending">Pending</SelectItem>
                        <SelectItem value="failed">Failed</SelectItem>
                    </SelectContent>
                </Select>

                {/* Reset & Export */}
                {hasActiveFilters && (
                    <Button
                        variant="outline"
                        size="sm"
                        onClick={handleReset}
                        className="rounded-xl"
                    >
                        <X className="w-4 h-4 mr-1" />
                        Reset
                    </Button>
                )}

                {onExport && (
                    <Button
                        variant="outline"
                        size="sm"
                        onClick={onExport}
                        className="rounded-xl"
                    >
                        Export CSV
                    </Button>
                )}
            </div>

            {/* Custom Date Range Picker */}
            {showCustomDate && (
                <div className="flex items-center gap-3 p-4 bg-gray-50 rounded-xl border border-gray-200">
                    <span className="text-sm font-medium text-muted-foreground">Custom Range:</span>

                    <Popover>
                        <PopoverTrigger asChild>
                            <Button
                                variant="outline"
                                className={cn(
                                    "justify-start text-left font-normal rounded-xl",
                                    !filters.customStartDate && "text-muted-foreground"
                                )}
                            >
                                <CalendarIcon className="mr-2 h-4 w-4" />
                                {filters.customStartDate ? format(filters.customStartDate, "PPP") : "Start date"}
                            </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                                mode="single"
                                selected={filters.customStartDate}
                                onSelect={(date) => onFiltersChange({ ...filters, customStartDate: date })}
                                initialFocus
                            />
                        </PopoverContent>
                    </Popover>

                    <span className="text-muted-foreground">to</span>

                    <Popover>
                        <PopoverTrigger asChild>
                            <Button
                                variant="outline"
                                className={cn(
                                    "justify-start text-left font-normal rounded-xl",
                                    !filters.customEndDate && "text-muted-foreground"
                                )}
                            >
                                <CalendarIcon className="mr-2 h-4 w-4" />
                                {filters.customEndDate ? format(filters.customEndDate, "PPP") : "End date"}
                            </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                                mode="single"
                                selected={filters.customEndDate}
                                onSelect={(date) => onFiltersChange({ ...filters, customEndDate: date })}
                                initialFocus
                            />
                        </PopoverContent>
                    </Popover>
                </div>
            )}
        </div>
    );
}
